*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 40px;
}
input{
  color: rgb(40, 140, 233);
  height: 20px;
  width: 100%;
  font-size: 1.3rem;
  padding: 5px;

}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}